<template>
  <div class="search">
    <el-form
      label-position="left"
      label-width="85px"
      :model="formData"
      class="search__form"
    >
      <el-row :gutter="40">
        <el-col :span="5">
          <el-form-item label="问诊编号">
            <el-input
              v-model="formData.out_trade_no"
              placeholder="请输入问诊编号"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="点评时间">
            <el-date-picker
              v-model="formData.createdDateRange"
              type="datetimerange"
              start-placeholder="请选择时间范围起始"
              end-placeholder="请选择时间范围结束"
              :default-time="['00:00:00', '23:59:59']"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="评价星级">
            <el-select
              v-model="formData.stars"
              multiple
              placeholder="请选择"
              size="small"
              clearable
            >
              <el-option
                v-for="item in selectOptsMaps.allStarStatus"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="40">
        <el-col :span="8">
          <el-form-item label="被评价人">
            <el-select
              v-model="formData.admin_users"
              multiple
              placeholder="请选择"
              size="small"
              clearable
            >
              <el-option
                v-for="item in selectOptsMaps.evaluatePeople"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="科室">
            <el-select
              v-model="formData.section_office"
              multiple
              placeholder="请选择"
              size="small"
              clearable
            >
              <el-option
                v-for="item in selectOptsMaps.department"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8" style="float:right;">
          <el-button type="primary" size="small" @click="handleSearch"
            >搜索评价</el-button
          >
          <el-button type="primary" plain size="small" @click="handleReset"
            >重置选项</el-button
          >
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="7">
          <el-form-item label="关键字搜索" label-width="85px">
            <el-input
              v-model="formData.search_key"
              placeholder="请输入关键字"
              size="small"
              @input="keywordChange"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="3">
          <el-tooltip
            content="关键字搜索条件不支持与上述评价相关的搜索条件一起搜索，清空后再进行其他条件的搜索。"
            placement="top"
            effect="dark"
          >
            <i
              class="el-icon-warning"
              style="margin:6px 5px 0;font-size: 16px;"
            />
          </el-tooltip>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
import _ from 'lodash'
const defaultformData = {
  out_trade_no: '',
  createdDateRange: [],
  section_office: [],
  admin_users: [],
  stars: [],
  search_key: '',
  currentPage: 1,
  pageSize: 10
}

export default {
  props: {
    curStarStatus: {
      type: Array,
      default: () => []
    },
    selectOptsMaps: {
      type: Object,
      required: true,
      default: () => ({
        allStarStatus: [],
        evaluateLabel: [],
        evaluatePeople: [],
        department: []
      })
    }
  },
  data() {
    return {
      formData: {}
    }
  },
  watch: {
    curStarStatus(value) {
      this.formData.stars = value
    }
  },
  created() {
    this.formData = Object.assign({}, defaultformData)
  },
  methods: {
    // 搜索防抖动
    keywordChange: _.debounce(function() {
      this.handleSearch()
    }, 1000),
    handleSearch() {
      this.formData.currentPage = 1
      this.$emit('search', { searchParams: this.formData })
    },
    handleReset() {
      this.handleResetFormData()
    },
    handleResetFormData() {
      this.formData = Object.assign({}, defaultformData)
      this.handleSearch()
    }
  }
}
</script>

<style lang="scss">
.search {
  &__form {
    margin-bottom: -20px;
    .el-form-item {
      margin-bottom: 20px;
      &__label,
      &__content {
        height: 32px;
        line-height: 32px;
      }
    }
    .el-date-editor,
    .el-select {
      width: 100%;
    }
  }
}
</style>

<style lang="scss" scoped>
.search {
  &__form {
    &__source {
      display: flex;
      align-items: center;
    }
    &__source-scene,
    &__source-custom-value {
      width: 50%;
    }
    &__source-scene {
      padding-right: 14px;
    }
    &__source-custom-value {
      padding-left: 14px;
    }
  }
}
</style>
