<template>
  <div class="fd-team-manage">
    <div class="fd-team-manage__header">
      <search-form
        :cur-star-status="stars"
        :select-opts-maps="selectOpts"
        @search="handleSearch"
        ref="searchForm"
      />
    </div>
    <el-tabs
      v-model="curTabName"
      style="margin-top:44px;"
      @tab-click="handleTabClick"
    >
      <el-tab-pane
        v-for="{ label, name, num } in tabArr"
        :key="name"
        :name="name"
      >
        <el-badge slot="label" :value="num" :max="99" class="item">
          <el-button
            :class="
              name === 'pending' ? 'order-list__body__button--pending' : ''
            "
            type="text"
            size="medium"
            >{{ label }}</el-button
          >
        </el-badge>
      </el-tab-pane>
    </el-tabs>
    <div class="fd-team-manage__body">
      <div class="el-card-warpper fd-team-manage__body__list">
        <el-card class="el-card--no-shadow el-card--header-nbb el-card--flex">
          <el-table
            v-loading="loading"
            class="fd-team-manage__body__list__table sbl-table"
            :data="tableDate"
          >
            <el-table-column
              align="center"
              label="问诊编号"
              min-width="90"
              prop="question_ID"
              fixed
            />
            <el-table-column align="center" label="点评时间" min-width="140">
              <template slot-scope="scope">
                {{ scope.row.createDate }}
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              label="评价星级"
              min-width="70"
              prop="star"
            />
            <el-table-column
              align="center"
              label="评价内容"
              min-width="140"
              prop="content"
            >
              <template slot-scope="scope">
                <span class="row_ellipsis">
                  {{ scope.row.content }}
                </span>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              label="被评价人"
              min-width="120"
              prop="name"
            >
              <template slot-scope="scope">
                {{ scope.row.admin_user.name }}
                {{
                  scope.row.admin_user.single_ask_bus_role
                    ? '（' +
                      systemRole(scope.row.admin_user.single_ask_bus_role) +
                      '）'
                    : ''
                }}
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              label="科室"
              min-width="100"
              prop="name"
            >
              <template slot-scope="scope">
                {{ sectionOffice(scope.row.section_office, '', true) }}
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              label="操作"
              fixed="right"
              width="160"
            >
              <template slot-scope="scope">
                <div class="fd-team-manage__body__list-handle">
                  <el-button
                    type="text"
                    style="margin-left:10px;"
                    :class="scope.row.isOnServerStatus ? 'online-style' : ''"
                    @click="
                      changeOnlineStatusFun(
                        scope.row,
                        !scope.row.isOnServerStatus
                      )
                    "
                    >单击{{
                      scope.row.isOnServerStatus ? '下线' : '上线'
                    }}</el-button
                  >
                  <el-button
                    type="text"
                    style="margin-left:10px;"
                    @click="handleHistory(scope)"
                    >查看详情</el-button
                  >
                </div>
              </template>
            </el-table-column>
          </el-table>
          <div class="fd-team-manage__body__list__pagination">
            <el-pagination
              :current-page="page.currentPage"
              :page-sizes="pageSizes"
              :page-size="page.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="page.total"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
            />
          </div>
        </el-card>
      </div>
    </div>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="onlineDialogVisible"
      :before-close="cancelOnlineChangeFun"
      width="30%"
      center
    >
      <span style="line-height:20px;">{{ dialogContent }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirmChangeOnlineStatus">{{
          dialogConfirmBtnTxt
        }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { getList, changeOnlineStatus } from '@/api/evaluate'
import SearchForm from './components/search-form'
import _ from 'lodash'
import { getStarArr, getEvaluateLabelArr } from '@/utils'
import { sectionOffice, systemRole } from '@/utils/dictionary'
import { mapGetters } from 'vuex'
export default {
  name: 'DiagnoseList',
  components: { SearchForm },
  computed: {
    ...mapGetters(['allPeople'])
  },
  data() {
    return {
      curTabName: 'all',
      dialogTitle: '评论上线提醒',
      dialogConfirmBtnTxt: '确认上线',
      dialogContent: '',
      loading: false,
      onlineDialogVisible: false,
      stars: [],
      tableDate: [],
      selectOpts: {
        allStarStatus: this.getStarArr(),
        evaluateLabel: this.getEvaluateLabelArr(),
        evaluateRole: [],
        evaluatePeople: [],
        department: this.sectionOffice('', 'arr', true)
      },
      page: {
        total: 0,
        pageSize: 10,
        currentPage: 1
      },
      tabArr: [
        {
          label: '全部',
          name: 'all'
        },
        {
          label: '非5星',
          name: 'notFiveStar'
        },
        {
          label: '5星',
          name: 'fiveStar'
        }
      ],
      is_on_server: false,
      pageSizes: [10, 20, 50],
      query: {}
    }
  },

  created() {
    this.getFilter()
    this.handleSearch()
  },
  methods: {
    getStarArr,
    getEvaluateLabelArr,
    systemRole,
    sectionOffice,
    async getFilter(sectionType) {
      await this.$store.dispatch('filterPeopleByRole', 'singleAsk')
      this.selectOpts.evaluatePeople = this.allPeople
    },
    async handleTabClick() {
      const value = this.curTabName
      if (value === 'fiveStar') {
        this.stars = [5]
      } else if (value === 'notFiveStar') {
        this.stars = [1, 2, 3, 4]
      } else {
        this.stars = [1, 2, 3, 4, 5]
      }
      this.tableDate = []
      this.setPage({ currentPage: 1, pageSize: 10, count: 0 })
      this.handleSearch()
    },
    cancelOnlineChangeFun() {
      this.onlineDialogVisible = false
      this.handleSearch()
    },
    async changeOnlineStatusFun(row, status) {
      this.onlineDialogVisible = true
      this.activeId = row._id
      // 本次咨询只有一条评论时
      if (status) {
        // 上线
        this.is_on_server = true
        this.dialogTitle = '评论上线提醒'
        this.dialogContent =
          '操作成功后，该评论将会被展示在用户端，确认操作请单击下方【确认上线按钮】'
        this.dialogConfirmBtnTxt = '确认上线'
      } else {
        this.is_on_server = false
        this.dialogTitle = '评论下线提醒'
        this.dialogContent =
          '操作成功后，该评论将从用户端的评论展示区域移除，确认操作请单击下方【确认下线按钮】'
        this.dialogConfirmBtnTxt = '确认下线'
      }
    },
    confirmChangeOnlineStatus() {
      const params = {
        _id: this.activeId,
        is_on_server: this.is_on_server
      }
      if (!this.activeId) {
        return
      }
      changeOnlineStatus(params).then(res => {
        const operatorName = this.dialogConfirmBtnTxt
        this.onlineDialogVisible = false
        this.$message({
          message:
            res && res.data.result
              ? operatorName + '成功'
              : operatorName + '失败',
          type: res && res.data.result ? 'success' : 'error'
        })
        if (res.data.result) {
          this.activeId = ''
          this.handleSearch()
        }
      })
    },
    setTableData({ list }) {
      this.tableDate = list.map((item, index) => ({
        id: item._id ? item.id : String(index),
        question_ID: item.question_ID,
        _id: item._id ? item._id : index,
        section_office: item.section_office,
        admin_user: item.admin_user ? item.admin_user : '', // 被评价人
        star: item.star ? item.star : '-',
        createDate: item.created_at ? this.formatTime(item.created_at) : '-',
        content: item.content ? item.content : '无',
        isOnServerStatus: item.is_on_server ? item.is_on_server : false
      }))
    },

    setPage({ currentPage: currentPage, pageSize: pageSize, count: total }) {
      this.page.currentPage = currentPage
      this.page.pageSize = pageSize
      this.page.total = total
    },

    async handleSearch(pageQuery) {
      const _query =
        pageQuery && pageQuery.searchParams ? pageQuery.searchParams : {}
      if (_query.stars && _.indexOf(_query.stars, 5) !== -1) {
        if (_query.stars.length > 1) {
          this.curTabName = 'all'
          this.stars = [1, 2, 3, 4, 5]
        } else {
          this.curTabName = 'fiveStar'
          this.stars = [5]
        }
      } else if (_query.stars && _query.stars.length === 4) {
        this.curTabName = 'notFiveStar'
        this.stars = _query.stars
      } else if (_query.stars) {
        this.curTabName = 'all'
        this.stars = _query.stars
      }
      if (!_query.currentPage) _query.currentPage = this.page.currentPage
      if (!_query.pageSize) _query.pageSize = this.page.pageSize
      _query.beginTime =
        _query.createdDateRange && _query.createdDateRange.length > 0
          ? _query.createdDateRange[0]
          : ''
      _query.endTime =
        _query.createdDateRange && _query.createdDateRange.length > 0
          ? _query.createdDateRange[1]
          : ''
      if (pageQuery) {
        this.query = _query
      }
      if (!pageQuery) {
        this.query.currentPage = this.page.currentPage
        this.query.pageSize = this.page.pageSize
      }
      this.query.stars = this.stars
      this.loading = true
      const { data } = await getList(this.query)
      this.loading = false
      this.setPage({ ...data })
      this.setTableData({ ...data })
    },
    handleHistory({ row, $index }) {
      this.$router.push({ path: `/evaluate/diagnose/detail/${row._id}?` })
    },
    async handleCurrentChange(page) {
      this.page.currentPage = page
      await this.handleSearch()
    },

    async handleSizeChange(pageSize) {
      this.page.pageSize = pageSize
      await this.handleSearch()
    }
  }
}
</script>
<style lang="scss">
.fd-team-manage {
  .el-card__header {
    margin-bottom: 16px;
  }
  &__body {
    &__list {
      .el-card__body {
        height: calc(100% - 72px);
      }
    }
  }
  .el-form-item {
    &__label,
    &__content {
      line-height: 32px;
    }
  }
}
</style>

<style lang="scss" scoped>
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.fd-team-manage {
  min-height: 100%;
  &__header {
    &__search {
      margin-right: 30px;
      width: 220px;
    }
  }
  &__body {
    padding-top: 30px;
    height: calc(100% - 32px);
    &__list {
      overflow: auto;
      height: 100%;
      .el-card {
        height: 100%;
      }
      .sbl-table {
        &__name-col {
          display: inline-flex;
          align-items: center;
          line-height: 0;
          &__name {
            padding-right: 10px;
          }
          &__icon {
            font-size: 18px;
            color: #5ba5e8;
            cursor: pointer;
          }
        }
      }
      &__pagination {
        padding: 60px 0 40px;
        text-align: center;
      }
    }
    &__list-handle {
      display: inline-block;
      min-width: 110px;
      text-align: left;
    }
  }
}
</style>
